import initialState from './initial-state'

const authorizationReducer = (state = initialState.authorization, action) => {
  switch (action.type) {
    case 'SIGNIN_ERROR':
      return {
        ...state,
        error: action.payload,
      }
    case 'SIGNIN_SUCCESS':
      return {
        ...state,
        error: null,
      }
    case 'SIGNOUT_SUCCESS':
      return {
        ...state,
        error: null,
      }
    case 'SIGNUP_SUCCESS':
      return {
        ...state,
        error: null,
      }
    case 'SIGNUP_ERROR':
      return {
        ...state,
        error: action.payload,
      }
    case 'CLEAR_AUTH_ERROR':
      return {
        ...state,
        error: null,
      }
    default:
      return state
  }
}

export default authorizationReducer