import { combineReducers, createStore, applyMiddleware } from 'redux'
import { composeWithDevTools } from 'redux-devtools-extension/logOnlyInProduction'
import thunkMiddleware from 'redux-thunk'
import { reduxFirestore, getFirestore, firestoreReducer } from 'redux-firestore'
import { getFirebase, firebaseReducer } from 'react-redux-firebase'
import authorizationReducer from '../reducers/authorization-reducer'
import quizListReducer from '../reducers/quiz-list-reducer'
import activeQuizInfoReducer from '../reducers/active-quiz-info-reducer'
import profileReducer from '../reducers/profile-reducer'
import newQuizReducer from '../reducers/new-quiz-reducer'
import searchReducer from '../reducers/search-reducer'
import { reducer as formReducer } from 'redux-form'
import fbConfig from '../configs/firebase-config'
import firebase from 'firebase/app'

const reducers = combineReducers({
  authorization: authorizationReducer,
  quizList: quizListReducer,
  activeQuizInfo: activeQuizInfoReducer,
  newQuiz: newQuizReducer,
  profile: profileReducer,
  search: searchReducer,
  form: formReducer,
  firestore: firestoreReducer,
  firebase: firebaseReducer,
})

const store = createStore(
  reducers,
  composeWithDevTools(
    applyMiddleware(thunkMiddleware.withExtraArgument({ getFirebase, getFirestore })),
    reduxFirestore(firebase, fbConfig)
  )
)

export default store
