import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Container } from 'react-bootstrap'
import Preloader from '../../components/UI/Preloader/Preloader'
// import * as Containers from '../../containers'
// import CodePage from '../../components/code-page'

const AuthContainer = React.lazy(() => import('../../containers/Auth-container'));
const QuizListContainer = React.lazy(() => import('../../containers/Quiz-list-container'));
const ActiveQuizContainer = React.lazy(() => import('../../containers/Active-quiz-container'));
const QuizCreatorContainer = React.lazy(() => import('../../containers/Quiz-creator-container'));
const ProfileContainer = React.lazy(() => import('../../containers/Profile-container'));
const SearchContainer = React.lazy(() => import('../../containers/Search-container'));


const Content = () => {
  return (
    <>
      <main className="d-flex flex-column flex-grow-1 main">
        <Container fluid="xl">
          <React.Suspense fallback={<Preloader />}>
            <Switch>
              <Route path='/auth/:mode'><AuthContainer /></Route>
              <Route path='/list'><QuizListContainer /></Route>
              <Route path='/quiz/:quizID'><ActiveQuizContainer /></Route>
              <Route path='/creator'><QuizCreatorContainer /></Route>
              <Route path='/profile'><ProfileContainer /></Route>
              <Route path='/search'><SearchContainer /></Route>
              {/* <Route path='/code' render={() => <CodePage />} /> */}
            </Switch>
          </React.Suspense>
        </Container>
      </main>
    </>
  )
}

export default Content