import initialState from './initial-state'

const quizListReducer = (state = initialState.quizList, action) => {
  switch (action.type) {
    case 'SET_QUIZ_LIST_INFO':
      return {
        ...state,
        quizListInfo: action.payload,
      }
    case 'SET_IS_FETCHING_LIST':
      return {
        ...state,
        isFetchingList: action.payload,
      }
    default:
      return state
  }
}

export default quizListReducer