import React from 'react'
import { Container } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import styles from './Start-page.module.css'

const StartPage = ({ auth }) => {
  return (
    <main className={styles['wrapper-bg']}>
      <Container fluid="xl" className="d-flex flex-grow-1 flex-column justify-content-center align-items-center">
          <div className={styles['quote-wrapper']}>
            <blockquote className={styles['blockquote']}>
              <p className="mb-0">"It does not matter how slowly you go as long as you do not stop."</p>
              <footer className={styles['blockquote-footer']}>Confucius</footer>
            </blockquote>
          </div>
          <div className={styles['btns-wrapper']}>
            {auth.uid
              ? (<>
                <Link to="/creator" className="btn btn-primary custom-bg mx-3 mb-3 btn-sm">Создать тест</Link>
                <Link to="/search" className="btn btn-outline-primary custom-bg mx-3 mb-3 btn-sm">Поиск по ID</Link>
              </>)
              : (<>
                <Link to="/auth/register" className="btn btn-primary custom-bg mx-3 mb-3 btn-sm">Зарегистрироваться</Link>
                <Link to="/list" className="btn btn-outline-primary custom-bg mx-3 mb-3 btn-sm">Без регистрации</Link>
              </>)}
          </div>
      </Container>
    </main>
  )
}

const mapStateToProps = ({ firebase }) => {
  return {
    auth: firebase.auth
  }
}

export default connect(mapStateToProps)(StartPage)