import initialState from './initial-state'

const testInfoReducer = (state = initialState.activeQuizInfo, action) => {
  switch (action.type) {
    case 'SET_ACTIVE_QUIZ_INFO':
      return {
        ...state,
        description: action.payload.description,
        tasksSheet: action.payload.sheet,
        quizID: action.payload.quizID
      }
    case 'SET_IS_FETCHING_QUIZ':
      return {
        ...state,
        isFetchingQuizInfo: action.payload,
      }
    case 'SET_IS_FETCHING_RESULT':
      return {
        ...state,
        isFetchingResult: action.payload,
      }
    case 'SET_STAGE':
      if (action.payload === null) {
        return {
          description: null,
          tasksSheet: null,
          answers: [],
          stage: null,
          quizID: null,
          activeTaskID: 0,
          selectedVariantID: null,
          isFetchingQuizInfo: false
        }
      }
      return {
        ...state,
        stage: action.payload,
      }
    case 'ADD_ANSWER':
      return {
        ...state,
        answers: [...state.answers, action.payload],
        selectedVariantID: action.payload.answerID,
      }
    case 'CHANGE_TASK':
      let nextTaskID = state.activeTaskID + 1
      return {
        ...state,
        activeTaskID: nextTaskID,
        selectedVariantID: null,
      }
    case 'SET_RESULT':
      return {
        ...state,
        result: action.payload,
      }
    default:
      return state
  }
}

export default testInfoReducer