import React from 'react'
import { Container } from 'react-bootstrap'
import './Footer.css'

const Footer = () => {
  return (
    <footer className="footer bg-primary">
      <Container fluid>
        <div className="text-center">
          <a href="https://github.com/mesmeri" className="github-link">
            mesmerizzze
        </a>
        </div>
      </Container>
    </footer>
  )
}

export default Footer