export const clearAuthError = () => ({ type: 'CLEAR_AUTH_ERROR' })
export const setSignupSuccess = () => ({ type: 'SIGNUP_SUCCESS' })
export const setSignupError = (error) => ({ type: 'SIGNUP_ERROR', payload: error })
export const setSigninSuccess = () => ({ type: 'SIGNUIN_SUCCESS' })
export const setSigninError = (error) => ({ type: 'SIGNIN_ERROR', payload: error })
export const setSignoutSuccess = () => ({ type: 'SIGNOUT_SUCCESS' })


export const signUp = (userData) => {
  return (dispatch, setState, { getFirebase, getFirestore }) => {
    const firebase = getFirebase()
    const firestore = getFirestore()

    firebase.auth().createUserWithEmailAndPassword(
      userData.email,
      userData.password
    )
      .then(response => {
        return firestore.collection('users').doc(response.user.uid)
          .set({
            login: userData.login,
            stats: [],
          })
          .then(() => {
            dispatch(setSignupSuccess())
          })
      })
      .catch(error => {
        dispatch(setSignupError(error.message))
      })
  }
}

export const signIn = (credentials) => {
  return (dispatch, getState, { getFirebase }) => {
    const firebase = getFirebase()
    firebase.auth().signInWithEmailAndPassword(
      credentials.email,
      credentials.password
    ).then((response) => {
      dispatch(setSigninSuccess())
    })
      .catch(error => {
        dispatch(setSigninError(error.message))
      })
  }
}

export const signOut = () => {
  return (dispatch, getState, { getFirebase }) => {
    const firebase = getFirebase()
    firebase.auth().signOut()
      .then(() => {
        dispatch(setSignoutSuccess())
      })
      .catch(error => console.log(error))
  }
}
