import initialState from './initial-state'

const profileReducer = (state = initialState.profile, action) => {
  switch (action.type) {
    case 'IS_FETCHING_MY_STATS':
      return {
        ...state,
        isFetchingMyStats: action.payload
      }
    case 'SET_MY_STATS':
      return {
        ...state,
        myStats: action.payload,
      }
    case 'IS_FETCHING_MY_QUIZES':
      return {
        ...state,
        isFetchingMyQuizes: action.payload
      }
    case 'SET_MY_QUIZES':
      return {
        ...state,
        myQuizes: action.payload,
      }
    case 'CLEAR_MY_DATA':
      return {
        myStats: null,
        myQuizes: null,
        isFetchingMyStats: false,
        isFetchingMyQuizes: false
      }
    default: 
      return state
  }
}

export default profileReducer