import React from 'react'
import { connect } from 'react-redux'
import { Container, Navbar, Nav, Button } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import Logo from '../../components/Logo/Logo'
import { signOut } from '../../reducers/action-creators/authorization-AC'

import './Header.css'

const Header = ({ auth, signOut, profile }) => {
  return (
    <header>
      <Navbar collapseOnSelect expand="sm" className="fixed-top bg-light custom-navbar-padding" fixed="top">
        <Container fluid="xl">
          <Link to="/" className="navbar-brand">
            <Logo />
          </Link>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="ml-3 navigation">
              <Link to="/list" className="nav-link">Все тесты</Link>
            </Nav>
            {profile.login && <Link to='/profile'><span className="login">{profile.login[0]}</span></Link>}
            {auth.uid
              ? <Button
                  onClick={signOut}
                  className="btn btn-secondary my-2 my-sm-0 logout-btn">Выйти</Button>
              : <Link
                  to="/auth/login"
                  className="btn btn-primary my-2 my-sm-0 login-btn">Войти</Link>}
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header >
  )
}

const mapStateToProps = ({ firebase }) => {
  return {
    auth: firebase.auth,
    profile: firebase.profile
  }
}

const mapDispatchToProps = {
  signOut,
}

export default connect(mapStateToProps, mapDispatchToProps)(Header)