import initialState from './initial-state'

const newQuizReducer = (state = initialState.newQuiz, action) => {
  switch (action.type) {
    case 'SET_DESCRIPTION':
      return {
        ...state,
        description: action.payload
      }
    case 'ADD_QUESTION':
      const newQuestion = {
        question: action.payload.question,
        variants: [
          action.payload.firstVariant,
          action.payload.secondVariant,
          action.payload.thirthVariant,
          action.payload.fourthVariant,
        ],
        correct: action.payload.correct,
        time: action.payload.time
      }
      if (action.payload.code) {
        newQuestion.code = action.payload.code
      }
      return {
        ...state,
        sheet: [
          ...state.sheet,
          newQuestion
        ]
      }
    case 'DELETE_QUESTION':
      const newSheet = [...state.sheet]
      newSheet.splice(action.payload, 1)
      return {
        ...state,
        sheet: [
          ...newSheet
        ]
      }
    case 'CLEAR_QUIZ':
      return {
        description: {
          chapter: "",
          topic: "",
        },
        sheet: [],
        error: null,
      }
    case 'QUIZ_CREATED_SUCCESS':
      return {
        ...state,
        error: null
      }
    case 'QUIZ_CREATED_ERROR':
      return {
        ...state,
        error: action.payload
      }
    default:
      return state
  }
}

export default newQuizReducer